            this.sevpic1[4].active = false;
            break;
          case 5:
            this.sevpic1[0].active = false;
            this.sevpic1[1].active = false;
            this.sevpic1[2].active = false;
            this.sevpic1[3].active = false;
            this.sevpic1[4].active = true;
            break;
          case 6:
            this.sevpic2[0].active = true;
            this.sevpic2[1].active = false;
            this.sevpic2[2].active = false;
            this.sevpic2[3].active = false;
            this.sevpic2[4].active = false;
            break;
          case 7:
            this.sevpic2[0].active = false;
            this.sevpic2[1].active = true;
            this.sevpic2[2].active = false;
            this.sevpic2[3].active = false;
            this.sevpic2[4].active = false;
            break;
          case 8:
            this.sevpic2[0].active = false;
            this.sevpic2[1].active = false;
            this.sevpic2[2].active = true;
            this.sevpic2[3].active = false;
            this.sevpic2[4].active = false;
            break;
          case 9:
            this.sevpic2[0].active = false;
            this.sevpic2[1].active = false;
            this.sevpic2[2].active = false;
            this.sevpic2[3].active = true;
            this.sevpic2[4].active = false;
            break;
          case 10:
            this.sevpic2[0].active = false;
            this.sevpic2[1].active = false;
            this.sevpic2[2].active = false;
            this.sevpic2[3].active = false;
            this.sevpic2[4].active = true;
            break;
          default:
            this.sevpic1[0].active = true;
            this.sevpic2[0].active = true;
        }

      },
      zhuc() {
        this.$router.push({
          path: '/zhuc'
        })
      },
			product(site) {
				this.$router.push({
					path: '/product',
					query:{
						site:site
					}
				})
			},
			service_details(id,name) {
				this.$router.push({
					path: '/service_details',
					query:{
						id:id,
						name:name,
						applyType:'3'
					}
				})
			},
			qiugou() {
				this.$router.push({
					path: '/qiugou'
				})
			},
			gj_service() {
				this.$router.push({
					path: '/gj_service'
				})
			},
			service() {
				this.$router.push({
					path: '/service'
				})
			},
			sb_details(id) {
				this.$router.push({
					path: '/sb_details',
						query:{
							id:id,
							applyType:'1'
						}
				})
			},

			com_new() {
				this.$router.push({
					path: '/com_new'
				})
			},
			sb_ask() {
				this.$router.push({
					path: '/sb_ask'
				})
			},
			sb_know() {
				this.$router.push({
					path: '/sb_know'
				})
			},
			sb_law() {
				this.$router.push({
					path: '/sb_law'
				})
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},

		},
		props: {},
		components: {
      topHeader,
			Phone,
      topMeau,
      Customer_service
		},
		mounted() {
			this.configgetList();
		}
	}
</script>

