<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p >
        <span >{{products.name}}</span>
        <u class="urltitle" @click="zhuanliku">专利库 </u>>
        <u class="urltitle" @click="zhuanlimeau">专利类型</u>>
        <u class="urltitle" @click="zhuanlilist">{{categoryFir}}·{{categorySec}}</u>>
        <b class="urltitle">{{products.name}}</b>
      </p>
    </div>
		<Content v-model="products"  style="margin-top: 20px">
			<div class="hm_product_num">
        <span class="buyseve">
        <buyservice :price="price" :flag="this.oldflag"></buyservice>
        </span>
        <p class="imgbg"><img :src=products.bannerUrl class="img" /></p>
				<Row type="flex" :gutter="24" class="de_cont">
					<Col span="6">
					<p>商标价格:{{products.price}}</p>
					</Col>
					<Col span="6">
					<p>服务费:{{servicePrice}}</p>
					</Col>
					<Col span="6">
					<p>申请号:{{products.applyNo}}</p>
					</Col>
					<Col span="6">
					<p>专利类型:{{products.categoryFir}}</p>
					</Col>
				</Row>

				<p class="line"></p>
				<Row type="flex" :gutter="24" class="de_cont">
					<Col span="6">
					<p>申请时间: {{products.createTime}}</p>
					</Col>
					<Col span="6">
					<p>交易类型:{{products.transactionType}}</p>
					</Col>

					<Col span="6">
					<p>行业分类:{{products.categorySec}}</p>
					</Col>
					<Col span="6">
					<p>编号:{{products.id}}</p>
					</Col>
				</Row>
        <p class="line"></p>
        <Button type="primary" shape="circle">联系方式:{{products.customerMobile}}</Button>
				<img src="../../assets/zl1.jpg" class="img1" />
				<div class="de_cont2">
					<img src="../../assets/zl4.jpg" class="img2" />
					<img src="../../assets/zl3.jpg" class="img3" />
					<img src="../../assets/zl2.jpg" class="img4" />
				</div>

				<Row type="flex" :gutter="16" class="de_cont3">
					<Col span="8">
					<p class="de_box">发明型专利<span class="blue"> ¥{{servicePrice}}</span></p>
					</Col>
					<Col span="8">
					<p class="de_box">实用型专利<span class="blue"> ¥{{servicePrice}}</span></p>
					</Col>
					<Col span="8">
					<p class="de_box">外观型专利<span class="blue"> ¥{{servicePrice}}</span></p>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	import {
		patentgetById,Collectlist
	} from '../../api/api';
	export default {
		name: 'zl_details',
		data() {
			return {
				name: '',
				bannerUrl: '',
        price:this.price,
        categoryFir:this.$route.query.categoryFir,
        categorySec:this.$route.query.categorySec,
				servicePrice:sessionStorage.patent_fee,
				id: this.id,
        flag:'',
        oldflag:'',
				applyNo: '',
				createTime: '',
				transactionType: '',
				customerMobile: '',
				products: [],

			}
		},
		components: {
			buyservice,
      topHeader,
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				this.listLoading = true;
				//NProgress.start();
				let applyType = this.$route.query.applyType;
				let para = {
					id: this.$route.query.id,
				};
				patentgetById(para).then((res) => {
					this.products = res.data.result;
          this.price = this.products.price;
          console.log(this.price)
          this.id = this.products.id;
          let para = {
            customerId: sessionStorage.customerId,
            actionType:'2',
            contentType:'2'
          };
          Collectlist(para).then((res) => {
            this.zl_list = res.data.result.result.patents;
            let charr = []
            this.zl_list.forEach(value => {
              console.log(value.id)
              if (this.id ===value.id){
                this.flag = true
              } else {
                this.flag = false
              }
              charr.push(this.flag)
            })
            if(charr.indexOf(true) !== -1){
              this.oldflag=true
            } else{
              this.oldflag=false
            }
            console.log(this.oldflag)
            this.listLoading = false;
            //NProgress.done();
          })
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
      zhuanliku(){
        this.$router.push({
          path: '/productzl',
        })
      },
      zhuanlimeau(){
        this.$router.push({
          path: '/zl_meau',
          query:{
            categoryFir:this.$route.query.categoryFir,
            categorySec:this.$route.query.categorySec
          }
        })
      },
      zhuanlilist(){
        this.$router.push({
          path: '/zl_list',
          query:{
            categoryFir:this.$route.query.categoryFir,
            categorySec:this.$route.query.categorySec
          }
        })
      },
		},
    watch: {
      'oldflag': function(newVal) {
        this.oldflag = newVal
      }
    },
		props: {},
		mounted() {
      this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
  .urltitle{
    font-size: 14px;
    cursor: pointer
  }
  button{
    width: 40%;
    margin: 30px 30%;
  }
  .img1,
  .img2,
	.img3,
	.img4 {
		margin: 10px 0 0 0;
		width: 100%;
	}

	.top {
		margin-top: 200px;
	}

	.hm_product_num {
    position: relative;
		width: 1200px;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
    .imgbg{
      width: 100%;
      height: 350px;
      display: flex;
      overflow: hidden;
      align-items: center;
      .img {
        width: 100%;
        display: block;
      }
    }
    .buyseve{
      width: 270px;
      position: absolute;
      right: 20px; top: 20px;
      padding: 10px 20px;
      background-color: #ffffff;
      border-radius: 30px
    }
		overflow: hidden;
		.de_cont {
			padding: 20px;
		}
		.de_cont3 {
			padding: 0 20px 20px 20px;
			.de_box {
				font-size: 18px;
				color: #000000;
				text-align: center;
				line-height: 25px;
				background: #FFFFFF;
				font-weight: bold;
				box-shadow: 0 0 6px 0 rgba(162, 162, 162, 0.50);
				border-radius: 3px;
        padding: 20px 0;
				display: block;
				width: 95%;
				margin: 0 5%;
				.blue {
					color: #3033F6;
					text-align: center;
				}
			}
		}
		span {
			display: block;
			text-align: left;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			text-align: left;
			padding: 7px;
		}
		.line {
			width: 100%;
			background-color: #f6f6f6;
			height: 1px;
			padding: 0;
		}
		.de_cont2 {
			padding: 20px;
			.title {
				font-size: 28px;
				color: #000000;
				font-weight: bold;
				height: 45px;
				line-height: 45px;
				text-align: center;
			}
			.smal-title {
				font-size: 13px;
				color: #9B9B9B;
				text-align: center;
			}
			.title2 {
				font-size: 18px;
				color: #2B2EF5;
				line-height: 18px;
				font-weight: bold;
				margin-top: 25px;
				margin-bottom: 8px;
			}
			span {
				font-size: 14px;
				color: #000000;
				line-height: 20px;
			}
		}
	}
</style>



