<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <span class="topmibanner" v-if="sale==0"><img src="../../assets/mibanner7.jpg"/></span>
    <div class="top_banner">
      <p >
        <span >{{categoryFir}}</span>
        <u class="urltitle" @click="shangbk">
          {{areaCode}}商标库</u> >
        <u class="urltitle" @click="shangall">
          {{areaCode}}商标全部分类</u>>
        <b class="urltitle">{{areaCode}}{{categoryFir}}</b>
      </p>
    </div>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
        <Row type="flex" :gutter="24" class="box_ul">
          <Col span="8"   class="item" v-for="(item, name, index) in product_list"  :key="index">
						<img  @click='sb_details(item.id,item.name,item.price)' :src="item.bannerUrl" />
						<span  @click='sb_details(item.id,item.name,item.price)' ><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
					</Col>
          <Col span="24" v-if="product_list.length > 0"><Nomore></Nomore></Col>
          <Nochild v-if="product_list.length === 0"></Nochild>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import { brandgetList } from '../../api/api';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
  import Nomore from "../public/Nomore";
  import Nochild from "../public/Nochild";
	export default {
		name: 'sb_list',
		data() {
			return {
				product_list: [],
				categoryFir:this.$route.query.categoryFir,
        areaCode:this.$route.query.areaCode,
        sale:this.$route.query.sale
			}
		},
		components: {
      Nomore,Nochild,
      topHeader,
      topMeau},
		props: {},
		methods: {
			//获取产品列表
			getProducts() {
				let categoryFir = this.$route.query.categoryFir;
				let vipFlag = this.$route.query.vipFlag;
				let inlandFlag = this.$route.query.inlandFlag;
				let areaCode = this.$route.query.areaCode
				//console.log(this.$route.query.areaCode);
				let para = {
					categoryFir: categoryFir,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
					vipFlag:vipFlag,
					inlandFlag:inlandFlag,
          areaCode:areaCode,
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
				console.log(res.data.result.result);
				this.product_list = res.data.result.result;
				console.log(this.product_list.length);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
				// console.log(err);
				});
			},
      gettjProducts() {
        let para = {
          servicePrice: 1,
        };
        this.listLoading = true;
        //NProgress.start();
        brandgetList(para).then((res) => {
          console.log(res.data.result.result);
          this.product_list = res.data.result.result;
          console.log(this.product_list.length);
          this.listLoading = false;
          //NProgress.done();
        }).catch((err) => {
          // console.log(err);
        });
      },
			sb_details(id,name,price) {
				this.$router.push({
					path: '/sb_details',
					query: {
						id: id, // orderNum : this.searchData.orderNo
						applyType: this.$route.query.applyType,
						amount: this.$route.query.price,
            areaCode: this.areaCode,
            categoryFir:this.categoryFir,
						name: name,
						price: price,
					}
				})
			},
      shangbk(){
        this.$router.push({
          path: '/product',
          query: {
            areaCode: this.areaCode
          }
        })
      },
      shangall(){
        this.$router.push({
          path: '/sb_all',
          query: {
            areaCode: this.areaCode
          }
        })
      }
		},
		mounted() {
      if(this.$route.query.sale==0){
        this.gettjProducts();
      } else if(this.$route.query.sale==1){
        this.getProducts();
      }
		}
	}
</script>

<style lang="less" scoped>
  .urltitle{
    font-size: 14px;
    cursor: pointer
  }
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
    .nomore{
      width: 100%;
      text-align: center;
      padding: 30px 0;
      font-size: 20px;
    }
    .box_ul {
      width: 100%;
      margin-top: -16px;
      .item {
        padding: 25px 0;
        display: block;
        height: auto !important;
        min-height: 20px;
        overflow: hidden;
        &:last-child{
          border: none;
        }
        button {
          background: #F4F4F4;
          border: 1px solid #E5E5E5;
          border-radius: 23px;
          width: 47%;
          height: 33px;
          line-height: 21px;
          font-size: 16px;
          float: left;
          margin-top: 15px;
          color: #666;
          margin: 15px 2% 0px 1%;
        }
        button:hover {
          color: #FFFFFF;
          background: #2B2EF5;
          border: none;
        }
      }
      img {
        height: 180px;
        width: 100%;
        border-radius: 3px;
        display: flex;
        float: left;
        margin-bottom: 15px;
        border: 1px solid #f6f6f6;
        cursor: pointer;
      }
      span {
        display: inline-block;
        float: left;
        font-size: 16px;
        color: #000000;
        text-align: left;
        padding:0px 20px;
        width: 100%;
        cursor: pointer;
        .title {
          font-size: 20px;
          color: #000000;
          font-weight: bold;
        }
        .small_bh {
          font-size: 20px;
          color: #9B9B9B;
          float: right;
          display: inline-block;
        }
        .price {
          font-size: 18px;
          color: #2B2EF5;
          font-weight: bold;
          float: left;
          display: inline-block;
        }
      }
    }
	}
</style>



