<template>
  <div>
    <Content style="width: 100%; background-color: #272727; padding-top:30px;">
  <div class="sever_foot">
    <Row type="flex" :gutter="16">
      <Col  span="8"  v-for="(item,index) in sever8" :key="index">
        <div>
          <p >
            <span v-show="item.name!==''" class="name"> {{item.name}}</span>
            <span v-show="item.title!==''" class="title"> {{item.title}}</span>
          </p>
        </div>
      </Col>
    </Row>
    <span v-for="(item,index) in dblink" class="spanlink" :key="index"><a :href='item.src'>{{item.name}}</a> </span>
  </div>
      <Col  span="24" >
        <p style="height:30px;background-color: #3a3a3a; color:#5e5e5e; line-height: 30px; font-size: 12px">京ICP备16001175号</p>
      </Col>
</Content>
  </div>
</template>
<script>
  export default {
    name: 'fooTer',
    data() {
      return {
        sever8:[
          {name:'商标服务部', title:'010-57170315'},
          {name:'公司地址', title:'北京市回龙观工业示范园发展路8号'},
          {name:'客服电话', title:'010-80720072'},
        ],
        dblink:[
          {name:'国家知识产权商标局', src:'http://sbj.cnipa.gov.cn/'},
          {name:'国家市场监督管理总局', src:'http://www.samr.gov.cn/'},
          {name:'国家知识产权局', src:'http://www.cnipa.gov.cn/'}
        ]
      }
    },
    methods: {
      login(){
        this.$router.push({
          path: '/login',
        })
      },
      zhuc() {
        this.$router.push({
          path: '/zhuc'
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  a,a:hover{
    color: #666666;
  }
  .sever_foot{
    width: 1200px;
    margin: 0 auto;
    p{
      display: block;
      height: 60px;
      margin-bottom: 15px;
    }
  }
  .spanlink{
    border-top: #333 1px solid;
    display: inline-block;
    padding: 20px;
    color: #666666;
    font-size: 13px;
  }
  p{
    text-align: center;
    img{
      height: 20px;
      width: 25px;
      display: block;
      margin: 20px auto 10px auto;
    }
    .name{
      font-size: 16px;
      font-weight: bold;
      color: #535353;
      display: block;
      width: 100%;
      height: 30px;
      line-height: 30px;
      text-align: center;
    }
    .title{
      font-size: 18px;
      font-weight: bold;
      color: #666;
      display: block;
      width: 100%;
      height: 20px;
      line-height: 20px;
      text-align: center;
    }
  }
</style>



