<template>
  <div>
  <Content style="width: 100%; background-color:#ffffff">
    <Header class="header">
    <span>
      <p  style="width: 100%; height: 80px; line-height: 80px">
        <router-link to="/"><img  class="logo" src="../../assets/logo.png"/></router-link>
        <img class="animated flipInX anitest" style="margin-top: 30px; height: 45%; margin-left: 20px;" src="../../assets/slogin.png"/>
        <span class="top_phone">
        <Phone></Phone>
        </span>
      </p>
    </span>
    </Header>
<span class="header_meau">
    <Row type="flex" class="topmeau">
      <Col v-for="(item, key, index) in headlist" :key="key" :index="index" :span=item.width>
        <router-link tag='a' :to="item.src"  :class="item.active===1?'active':''">{{item.name}}<p v-if="item.small!==''" class="title-ys">{{item.small}}</p></router-link>
      </Col>
    </Row></span>
  </Content>
  </div>
</template>
<script>
  import Phone from './phone.vue';
  export default {
    name: 'topHeader',
    data() {
      return {
          headlist:[
            {src: '/', active: 0, name: '首页', small:'',width:'2'},
            {src: '/product?apply=0', active: 0, name: '商标超市', small:'',width:'2'},
            {src: '/sb_list?sale=0', active: 0, name: '每日特价标', small:'',width:'3'},
            {src: '/product?apply=1', active: 0, name: '名标专区', small:'hot',width:'3'},
            {src: '/product?apply=2', active: 0, name: '商标许可共享专区', small:'',width:'4'},
            {src: '/service', active: 0, name: '商标服务项目', small:'',width:'3'},
            {src: '/productzl', active: 0, name: '专利超市', small:'new',width:'3'},
            {src: '/news', active: 0, name: '商标资讯', small:'',width:'2'},
            {src: '/service_details?id=34&name=公司介绍&applyType=3', active: 0, name: '公司简介', small:'',width:'2'}
            ]
      }
    },
    components: {
      Phone
    },
    methods: {
    }
  }
  </script>
<style lang="less" scoped>
  .anitest{
    animation-duration: 3s;
    animation-delay: 0.1s;
    animation-iteration-count: infinite;
  }
  .header_meau{
    background-color: #e70000;
    height: 60px;
    line-height: 60px;
    display: block;
    color: #ffffff;
    .topmeau{
      text-align: left;
      width: 1200px;
      margin: 0 auto;
      height: 60px;
      color: #ffffff;
      line-height: 60px;
      a{
        color: #ffffff;
        font-size: 18px;
        font-weight: bold;
      }
    }
  }
    .top_phone{
      width: 400px;
      display: block;
      float: right;
      top: 66px;
      position: absolute;
      right: 126px
    }
      .active{
        color: #0000FF;
      }
  .title-ys{
    display: block;
    height: 12px;
    line-height: 12px;
    padding: 0 2px;
    background-color: #FFFFFF;
    color: #e70000;
    text-align: center;
    font-size: 10px;
    position: absolute;
    border-radius: 1px;
    top: 18px;
    right: 45px;
  }
  </style>



