<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <!--<span v-show="id !== '34'" class="topmibanner"><img src="../../assets/mibanner7.jpg"/></span>-->
    <span v-show="id === '34'" class="topmibanner"><img src="../../assets/mibanner6.jpg"/></span>
    <div class="top_banner"  v-show="id !== '34'">
      <p >
        <span>{{service.name}}</span>
      </p>
      <u  class="urltitle" @click="guoneifuwu">国内服务</u>
      >
      <b  class="urltitle">{{service.name}}</b>
    </div>
		<Content class="content6" :model="service">
      <div class="hm_product_num">
        <p class="imgbg"><img class="bg_box" :src=service.bannerUrl /></p>
        <Row type="flex" :gutter="24" class="de_cont"  v-show="id !== '34'" style="box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 2px">
          <Col span="12">
            <p>价格: <span class="cl_num">{{service.price}}</span></p>
          </Col>
          <Col span="6">
            <p>商品编号:<span>{{service.id}}</span></p>
          </Col>
          <Col span="6">
            <buyservice :name="name" :price="newprice"></buyservice>
          </Col>
        </Row>
				<Row v-if="id !== '34'"  type="flex" :gutter="24" class="de_cont2">
          <Col span="5">
           <router-link to="/product"><img src="../../assets/left1.jpg" /></router-link>
            <router-link to="/productzl" ><img src="../../assets/left2.jpg" /></router-link>
            <router-link to="/news"><img src="../../assets/left3.jpg" /></router-link>
            <router-link to="/service_details?id=34&name=公司介绍&applyType=3"><img src="../../assets/left5.jpg" /></router-link>
          </Col>
					<Col span="19" class="right_content">
            <img src="../../assets/nr1.jpg" />
					<p class="cont" v-html='service.content' ></p>
            <img src="../../assets/nr2.jpg" />
            <img src="../../assets/nr3.jpg" />
            <img src="../../assets/nr4.jpg" />
            <img src="../../assets/nr5.jpg" />
					</Col>
				</Row>
        <Row v-else-if="id === '34'" type="flex" :gutter="24" class="de_cont2">
          <Col span="24">
            <p class="cont" v-html='service.content' ></p>
          </Col>
        </Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import { servicegetList, servicecreate, serviceupdate, servicedelete, servicegetById } from '../../api/api';
	import buyservice from './../public/buyservice.vue';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'service_details',
		data() {
			return {
				name:this.name,
        title:'',
				price:this.price,
        newprice:this.newprice,
				total:'',
				content: '',
				bannerUrl:'',
        id:this.$route.query.id,
				service:[],
        ids:this.$route.query.id
			}
		},
		components: {
			buyservice,
      topHeader,
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
      guoneifuwu(){
        this.$router.push({
          path: '/service',
        })
      },
			//获取产品类别列表
			getservice() {
				let para = {
					id:this.$route.query.id
				};
				this.listLoading = true;
				servicegetById(para).then((res) => {
					this.service = res.data.result;
					this.price=this.service.price;
          this.name=this.service.name;
          this.newprice = Number(this.price) - Number(sessionStorage.brand_fee);
					this.listLoading = false;
				});
			},
		},
		props: {},
		mounted() {
		  console.log(this.service)
			this.getservice();
			console.log(this.ids)
      if(this.ids=='38'){
        this.name='商标交易'
        this.title='品类丰富'
      } else if(this.ids=='55') {
        this.name='专利交易'
        this.title='品类丰富'
      }
		}
	}
</script>

<style lang="less" scoped>
  .urltitle{
    font-size: 14px;
    cursor: pointer
  }
	.bg_box {
		width: 100%;
		height: 100%;
		display: block;
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 122px;
		font-weight: bolder;
	}

	.top {
		margin-top: 152px;
	}

	.hm_product_num {
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
    .imgbg{
      width: 100%;
      height: auto;
      display: flex;
      overflow: hidden;
      align-items: center;
      .img {
        width: 100%;
        display: block;
      }
    }
	    .de_cont {
			padding:15px 0px 15px 0px;
        display: flex;
        align-items: center;
		}
    .de_cont2 {
      padding:15px 0px 25px 0px;
      img{
        margin-bottom: 5px;
        width: 100%;
      }
    }
    .right_content{
      img{
        width: 100%;
      }
    }
		.cl_num{
			color: #2B2EF5;
			font-size: 26px;
		}
		span {
			display: inline-block;
			text-align: left;
			font-weight: bold;
		}
		p {
			font-size: 22px;
			color: #000000;
			line-height: 22px;
			text-align: left;
		}
		.cont {
			font-size: 14px;
			color: #000000;
			width: 100%;
			line-height: 25px;
      margin-bottom: 20px;
      margin-top: 10px;
      p{
        img{
          width: 100%;
          display: inline-block;
          margin: 0 auto;
        }
      }
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
	}
</style>



