<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <span class="topmibanner"><img :src=imgsrc /></span>
    <div class="top_banner">
      <p >{{name}}
        <span>{{title}}</span>
        <u v-if="areaCode !==undefined" @click="changecity">{{areaCode}}></u>
        <u v-if="areaCode ===undefined" @click="changecity">切换城市></u>
        </p>
    </div>
		<Content class="content2 top_p bottom_p" style="margin-bottom: 0px;">
			<div class="hm_product_num">
          <Row type="flex" :gutter="24"  v-if="status==0">
            <Col span="6"  v-for="(item, name, index) in sb_fl" :name="name" :key="index">
              <p  @click="product_list(item.categoryFir)">
                <span class="sty1_box" :class="item.class"><img :src=item.src /></span>{{item.name}}</p>
            </Col>
            <Col span="24">
              <a @click="sb_all" class="more">查看全部>></a>
            </Col>
          </Row>
          <Row type="flex" :gutter="16"  v-if="status==2">
            <Col span="6" v-for="(item, name, index) in sb_fl"  :name="name" :key="index">
              <p  @click="gj_product_list(item.categoryFir)">
                <span class="sty1_box" :class="item.class"><img :src=item.src /></span>{{item.name}}</p>
            </Col>
            <Col span="24">
              <a @click="sb_all_mb()" class="more">查看全部>></a>
            </Col>
          </Row>
          <Row type="flex" :gutter="16"   v-else-if="status==1">
            <Col span="6" v-for="(item, name, index) in sb_fl"  :name="name" :key="index">
              <p  @click="mb_product_list(item.categoryFir)">
                <span class="sty1_box" :class="item.class"><img :src=item.src /></span>{{item.name}}</p>
            </Col>
            <Col span="24">
              <a @click="sb_all_mb()" class="more">查看全部>></a>
            </Col>
          </Row>

        <!--<Tabs value="name1">-->
					<!--<TabPane label="商标" name="name1"   v-show="this.id='1'">-->
					<!--</TabPane>-->
					<!--<TabPane label="国际商标" name="name2" v-show="this.id='1'">-->
						<!--</TabPane>-->
					<!--<TabPane label="名标" name="name3" v-show="this.id='1'">-->
						<!--</TabPane>-->
				<!--</Tabs>-->
			</div>
		</Content>
	</div>
</template>

<script>
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'product_list',
		data() {
			return {
        areaCode:this.$route.query.areaCode,
        id:'',
        sale:1,
        imgsrc:'',
        status:'',
				  name:'',
          value:'',
          title:'',
          slogin:'',
				sb_fl: [{
						categoryFir: '01类-化学原料',
						name: '01类-化学原料',
						src: require('../../assets/sb_1.png'),
            class: 'sty1'
					},
					{
						categoryFir: '02类-涂料油漆',
						name: '02类-涂料油漆',
						src: require('../../assets/sb_2.png'),
            class: 'sty2'
					},
					{
						categoryFir: '03类-日化用品',
						name: '03类-日化用品',
						src: require('../../assets/sb_3.png'),
            class: 'sty3'
					},
					{
						categoryFir: '04类-燃料油脂',
						name: '04类-燃料油脂',
						src: require('../../assets/sb_4.png'),
            class: 'sty2'
					},
					{
						categoryFir: '05类-医药品',
						name: '05类-医药品',
						src: require('../../assets/sb_5.png'),
            class: 'sty3'
					},
					{
						categoryFir: '06类-五金金属',
						name: '06类-五金金属',
						src: require('../../assets/sb_6.png'),
            class: 'sty4'
					},
					{
						categoryFir: '07类-机械设备',
						name: '07类-机械设备',
						src: require('../../assets/sb_7.png'),
            class: 'sty4'
					},
					{
						categoryFir: '08类-手工器械',
						name: '08类-手工器械',
						src: require('../../assets/sb_8.png'),
            class: 'sty1'
					}
				],
				gjsb: [
				{
						areaCode: '美国商标',
						name: '美国商标',
						src: require('../../assets/meiguo.png'),
					},{
						areaCode: '欧盟商标',
						name: '欧盟商标',
						src: require('../../assets/oumeng.png'),
					},
					{
						areaCode: '德国商标',
						name: '德国商标',
						src: require('../../assets/deguo.png'),
					},
					{
						areaCode: '法国商标',
						name: '法国商标',
						src: require('../../assets/faguo.png'),
					},
					{
						areaCode: '香港商标',
						name: '香港商标',
						src: require('../../assets/xiangg.png'),
					},
					{
						areaCode: '台湾商标',
						name: '台湾商标',
						src: require('../../assets/taiwn.png'),
					},
					{
						areaCode: '韩国商标',
						name: '韩国商标',
						src: require('../../assets/hanguo.png'),
					},
					{
						areaCode: '日本商标',
						name: '日本商标',
						src: require('../../assets/riben.png'),
					},
					{
						areaCode: '其他国家',
						name: '其他国家',
						src: require('../../assets/othercon.png'),
					}
				],
			}
		},
    components: {
      topHeader,topMeau
    },
    mounted() {
      if(this.$route.query.apply==0){
        this.id=0
        this.value ='商标超市'
        this.title='品类丰富 · 数量多'
        this.slogin='商标转让、许可的自由买卖平台'
        this.imgsrc= require('../../assets/sbcs.jpg')
      } else if(this.$route.query.apply==1){
        this.id=1
        this.value ='名标专区'
        this.title='品类丰富 · 数量多'
        this.slogin='商标转让、许可的自由买卖平台'
        this.imgsrc=require('../../assets/mbzq.jpg')
      } else if(this.$route.query.apply==2){
        this.id=2
        this.value ='商标许可共享专区'
        this.title='品类丰富 · 数量多'
        this.slogin='商标转让、许可的自由买卖平台'
        this.imgsrc=require('../../assets/mibanner1.jpg')
      } else{
        this.value ='商标超市'
        this.title='品类丰富 · 数量多'
        this.slogin='商标转让、许可的自由买卖平台'
        this.imgsrc= require('../../assets/sbcs.jpg')
      }
      console.log(this.$route.query.apply)
      console.log(this.value)
      console.log(this.id)
    },
    watch: {
      value(val) {
        this.name = this.value;
      },
      id(val){
        this.status=this.id;
      }
    },
		methods: {
      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h('p', null, [
            h('span', null, '内容可以是 '),
            h('i', { style: 'color: teal' }, 'VNode')
          ]),
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '执行中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
			product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'Y',
                    areaCode:this.$route.query.areaCode,
                    sale:this.sale
                    }
				})
				} else{
					this.$router.push({
					path: '/sb_all',
            query: {
              areaCode: this.$route.query.areaCode,
              sale:this.sale
            }
				})
				}
			},

			mb_product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir,
                    vipFlag:'Y',
                    applyType:'1',
                    areaCode:this.$route.query.areaCode,
                    sale:this.sale
                    }
				})
				} else{
					this.$router.push({
					path: '/sb_all',
                    query:{
                    vipFlag:'Y',
                    areaCode:this.$route.query.areaCode,
                     sale:this.sale
                    }
				})
				}
			},

			gj_product_list(categoryFir) {
				this.$router.push({
					path: '/sb_list',
          query:{
          categoryFir:categoryFir, // orderNum : this.searchData.orderNo
          applyType:'1',
          publicFlag:'Y',
          areaCode:this.$route.query.areaCode,
          sale:this.sale
          }
				})
			},
			sb_all() {
				this.$router.push({
					path: '/sb_all',
          query: {
            areaCode: this.$route.query.areaCode,
            sale:this.sale
          }
				})
			},
			sb_all_mb() {
				this.$router.push({
					path: '/sb_all',
					query:{
                    inlandFlag:'Y',
                    areaCode:this.$route.query.areaCode,
                    sale:this.sale
                    }
				})
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
      changecity(){
        this.$router.push({
          path: '/changecity'
        })
      }
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 1200px;
    margin: 0 auto;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
    .sty1{
      background-color: #52b998;
    }
    .sty2{
      background-color: #1969f6;
    }
    .sty3{
      background-color: #5f42e2;
    }
    .sty4{
      background-color: #ee5b36;
    }
    .sty1_box{
      line-height: 130px;
      margin: 0 10% 15px 10%;
      width: 80%;
      align-items: center;
      display: flex;
      border-radius: 8px;
      height: 130px;
    }
    .icon_box{
      height: 130px;
      line-height: 130px;
      margin: 0 10% 15px 10%;
      width: 80%;
      align-items: center;
      display: flex;
      border-radius: 8px;
      box-shadow: 0px 0px 8px #e1e1e1;
    }
    .more{
      width: 100%;
      height: 35px;
      line-height: 35px;
      background-color: #e6e6e6;
      font-size: 14px;
      display: block;
    }
		p {
      font-size: 16px;
			color: #000000;
			text-align: center;
			margin: 16px 0 26px 0;
      cursor: pointer;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>



